<template>
  <div
    class="popup popup-level-losing"
    :class="{opened}"
    @transitionend="hideContent"
  >
    <div
      v-show="shownContent"
      class="popup-content"
      :class="{'show-replay-game': showReplayGame}"
    >
      <div class="popup-content-border-wrapper">
        <div class="popup-content-border" />
      </div>
      <h2 class="title">
        {{ $t('components.popupLevelLosing.title') }}
      </h2>
      <p class="text1">
        {{ $t('components.popupLevelLosing.text1') }}
      </p>
      <p class="score-value-wrapper">
        <span class="score-value"> {{ internalScoreValue }}  </span>
        <span class="score-value-description"> {{ scoreValueDescription }} </span>
      </p>
      <p
        class="text2"
        v-html="$t('components.popupLevelLosing.text2', { maxScoreText }) "
      />
      <p
        class="text3"
        v-html="$t('components.popupLevelLosing.text3')"
      />
      <div class="buttons-wrapper">
        <button
          class="restart-button"
          @click="restartLevel"
        />
        <span
          class="restart-button-title"
          v-html="$t('components.popupLevelLosing.backButtonTitle1')"
        />
        <button
          class="restart-button restart-game-button"
          @click="restartGame"
        />
        <span
          class="restart-button-title restart-game-button-title"
          v-html="$t('components.popupLevelLosing.backButtonTitle2')"
        />
        <button
          class="main-button next-button"
          @click="goToNextLevel"
        >
          {{ $t('components.popupLevelLosing.nextButtonTitle') }}
        </button>
      </div>
    </div>
    <v-preload-hidden-images
      :images="[
        require('../../assets/media/components/popup-level-losing/next-button-hover.svg'),
        require('../../assets/media/components/popup-level-losing/next-button-active.svg'),
      ]"
    />
  </div>
</template>

<script>
import { numberEnd } from '@/utils';

export default {
  name: 'PopupLevelLosing',
  props: {
    opened: {
      type: Boolean,
      default: false,
    },
    scoreValue: {
      type: Number,
      default: 5,
    },
    maxScoreValue: {
      type: Number,
      default: 5,
    },
    showReplayGame: {
      type: Boolean,
      default: true,
    },
  },
  emits: ['update:opened', 'restart-game', 'goto-next-level'],
  data() {
    return {
      shownContent: false,
      internalScoreValue: 0,
    };
  },
  computed: {
    scoreValueDescription() {
      return numberEnd(this.internalScoreValue, [
        this.$t('components.popupLevelLosing.scoreTextType1'),
        this.$t('components.popupLevelLosing.scoreTextType2'),
        this.$t('components.popupLevelLosing.scoreTextType3'),
      ]);
    },
    maxScoreText() {
      const text = numberEnd(this.maxScoreValue, [
        this.$t('components.popupLevelLosing.scoreTextType1'),
        this.$t('components.popupLevelLosing.scoreTextType2'),
        this.$t('components.popupLevelLosing.scoreTextType3'),
      ]);
      return `${this.maxScoreValue} ${text}`;
    },
  },
  watch: {
    opened: {
      immediate: true,
      handler(value) {
        if (value) {
          this.internalScoreValue = this.scoreValue;
          this.showContent();
        }
      },
    },
  },
  methods: {
    closePopup() {
      this.$emit('update:opened', false);
    },
    showContent() {
      this.shownContent = true;
    },
    hideContent() {
      if (!this.opened) this.shownContent = false;
    },
    restartLevel() {
      this.closePopup();
      this.$emit('restart-level');
    },
    restartGame() {
      this.closePopup();
      this.$emit('restart-game');
    },
    goToNextLevel() {
      this.closePopup();
      this.$emit('goto-next-level');
    },
  },
};
</script>

